/* style for story */

.story {
    @include clearfix;
    .thumb {}
    .title {
        @include font-size(16, 22);
        font-weight: normal;
    }
    .cate {
        a {
            color: $brand-primary;
        }
    }
    .summary,
    .meta {
        color: #555;
        @include font-size(14, 19);
        >span {
            margin-right: 15px;
        }
        .fa {
            margin-right: 2px;
            font-size: 0.9em;
        }
    }
    .thumb,
    .cate,
    .title,
    .summary,
    .meta {
        margin-bottom: 5px;
    }
    // story with bold title
    &.story-bold {
        .title {
            font-weight: bold;
        }
    }
    // story cover
    &.story-cover {
        .title {
            @include font-size(20, 28);
            // margin-bottom: 8px;
        }
        //story include video
        .thumb {
            > [id^=video_] {
                width: 100% !important;
            }
        }
    }
    // story with left thumbnail
    &.story-left {
        .thumb {
            float: left;
            margin-right: 10px;
        }
        .title {
            margin-top: -3px;
        }
    }
    // story with red bullet 
    &.story-bullet {
        margin-bottom: 10px;
        @include last-child-y;
        .title {
            @include font-size(14, 19);
            &::before {
                content: '';
                display: inline;
                margin-right: 5px;
                font-family: "fontawesome";
            }
        }
    }
    // story with mask 
    &.story-mask {
        .thumb {
            z-index: 1;
        }
        > header {
            position: relative;
            padding: 10px;
            background: #fff;
            margin-top: -25px;
            margin-left: 10px;
            margin-right: 10px;
            z-index: 2;
        }
        &.story-cover {
            > header {
                padding: 20px;
                margin-top: -50px;
                margin-right: 20px;
                margin-left: 20px;
            }
        }
    }
    .sidebar .grid-zone & {
        .title {
            @include font-size(14, 19);
        }
    }
}

.item {
    border: 1px solid #eee;
    background-color: white;
    margin-bottom: 20px;
    @include transition(all, 0.2, ease-out);
    @include respond-to-md() {
        margin-bottom: 0;
    }
    &:hover {
        -webkikt-box-shadow: 0px 0px 10px 1px rgba(0, 0, 0, .16);
        box-shadow: 0px 0px 10px 1px rgba(0, 0, 0, .16);
    }
    &__info {
        position: relative;
        padding: 25px 15px 20px;
    }
    &__title {
        font-size: 18px;
        font-weight: bold;
    }
    &__summary {
        margin-top: 10px;
        font-size: 13px;
        p {
            margin-bottom: 5px;
        }
    }
    &__rating {
        .fa-star {
            color: #cecece;
            &.active {
                color: $brand-info;
            }
        }
    }
    &__viewmore {
        position: absolute;
        top: -15px;
        left: 0;
        right: 0;
        text-align: center;
        .btn {
            -webkikt-box-shadow: 0px 0px 10px 1px rgba(0, 0, 0, .16);
            box-shadow: 0px 0px 10px 1px rgba(0, 0, 0, .16);
        }
    }
    &--green {
        .fa-check-circle {
            color: $brand-danger;
        }
    }
    &--blue {
        .fa-check-circle {
            color: $brand-primary;
        }
    }
    &--yellow {
        .fa-check-circle {
            color: $brand-info;
        }
    }
}

@include Block(item) {
    @include clearfix();
    @include Modifier(sponsor) {
        @include Element(link) {
            &::after {
                content: '- Tài trợ';
                margin-left: 5px;
                display: inline;
                color: $brand-info;
                font-size: 13px;
                font-weight: normal;
            }
        }
    }
    @include Element(thumb) {
        @include transition();
        &:hover {
            opacity: 0.95;
        }
    }
    @include Modifier(has-shadow) {
        box-shadow: 0px 0px 10px 2px rgba(0, 0, 0, .16);
    }
    @include Modifier(horizontal) {
        margin-bottom: 30px;
        position: relative;
        @include Element(thumb) {
            float: left;
            width: 25%;
            position: relative;
        }
        @include Element(related-img) {
            position: absolute;
            left: 5px;
            bottom: 5px;
            li {
                width: 40px;
                height: 40px;
                overflow: hidden;
                float: left;
                margin-right: 5px;
                border: 1px solid #fff;
            }
        }
        @include Element(markup) {
            cursor: pointer;
            position: absolute;
            top: 5px;
            right: 5px;
            width: 26px;
            height: 26px;
            border-radius: 50%;
            background-color: rgba(0, 0, 0, .5);
            text-align: center;
            color: white;
            @include font-size(14, 26);
            &.is-marked {
                color: #d9534f;
            }
        }
        @include Element(title) {
            margin-bottom: 7px;
            a {
                margin-right: 10px;
            }
        }
        @include Element(rating) {
            display: inline-block;
        }
        @include Element(meta) {
            color: #7c7c7c;
            font-size: 13px;
            margin-bottom: 15px;
        }
        @include Element(ulti) {
            margin-bottom: 15px;
            @include clearfix();
            li {
                float: left;
                margin-right: 10px;
                border-radius: 3px;
                border: 1px solid #ddd;
                font-size: 14px;
                color: #7c7c7c;
            }
            .fa {
                padding: 12px;
            }
        }
        @include Element(meta-link) {
            margin-right: 15px;
            color: #7c7c7c;
            .fa {
                margin-right: 5px;
            }
            &:hover {
                color: $brand-primary;
            }
        }
        @include Element(info) {
            float: left;
            width: 55%;
            padding: 15px;
        }
        @include Element(review) {
            @include clearfix();
        }
        @include Element(score) {
            display: inline-block;
            background-color: #f7f7f7;
            padding: 5px 10px;
            border-radius: 4px;
            font-size: 12px;
            margin-right: 10px;
            b {
                margin-left: 5px;
                font-size: 24px;
                color: $brand-info;
            }
        }
        @include Element(comments) {
            display: inline-block;
            background-color: #f7f7f7;
            padding: 5px 10px;
            border-radius: 4px;
            font-size: 12px;
            margin-right: 10px;
            b {
                margin-left: 5px;
                font-size: 24px;
                color: $brand-primary;
            }
        }
        @include Element(addition) {
            text-align: center;
            width: 20%;
            float: left;
            padding: 25px 15px;
            &::after {
                display: block;
                content: '';
                position: absolute;
                top: 0;
                bottom: 0;
                left: 80%;
                width: 1px;
                background-color: #ddd;
            }
            .btn {
                font-size: 16px;
            }
        }
        @include Element(price) {
            margin-bottom: 10px;
            font-size: 18px;
            color: #d9534f;
            font-weight: bold;
        }
    }
}
